.first-screen {
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	position: relative;

	&__container {
		display: flex;
		justify-content: space-between;
		padding: 282px 0 81px;
	}

	&__content {
		position: relative;
		max-width: 580px;
	}

	&__title {
		font-size: 54px;
		line-height: 56.74px;
		font-weight: 300;
		padding-top: 11px;

		p {
			font-size: 42px;
			line-height: 58.34px;
			font-weight: 500;
		}

		span {
			font-weight: 300;
		}
	}

	&__price {
		display: inline-block;
		width: 145px;
		height: 41px;
		position: relative;
		top: 6px;
		left: 6px;

		&::before,
		&::after {
			content: '';
			display: block;
			position: absolute;
		}

		&::before {
			width: 100%;
			height: 100%;
			border-radius: 10px;
			box-shadow: 0 0 30px 10px rgba(220, 222, 237, 0.18), inset 0 0 35px rgba(255, 255, 255, 0.16);
			background-color: #7a9fc8;
			top: 0;
			left: 0;
		}

		&::after {
			width: 8px;
			height: 8px;
			background-color: $white;
			border-radius: 50%;
			top: 18px;
			left: 13px;
		}

		span {
			position: absolute;
			top: 1px;
			left: 32px;
			font-size: 14px;
			line-height: 41px;
			color: $white;
			font-weight: 400;
		}
	}

	&__note {
		padding-top: 55px;
		padding-left: 15px;
		position: relative;

		p {
			position: absolute;

			&:first-of-type {
				top: 31px;
				left: 200px;
				font-size: 139px;
				font-weight: 500;
				opacity: 0.05;
			}

			&:last-of-type {
				top: 45px;
				right: 79px;
				font-size: 25px;
				line-height: 32.62px;
				font-weight: 300;

				span {
					font-weight: 500;
				}
			}
		}

		img {
			position: relative;
			z-index: 1;
		}
	}

	&__man {
		position: absolute;
		bottom: 0;
		left: 52%;
		transform: translateX(-50%);
	}
}

.advantages {
	&__container {
		padding: 73px 0 0;
	}

	&__flex {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -50px;
	}
}

.advantages-item {
	width: calc(33.3333% - 100px);
	margin: 0 50px;
	text-align: center;

	&:nth-child(3n+2) {
		width: calc(35.3333% - 100px);
	}

	&:nth-child(3n+3) {
		width: calc(31.3333% - 100px);
	}

	img {
		margin: 0 auto;
	}

	p {
		font-size: 22px;
		font-weight: 300;
		line-height: 32.01px;
		letter-spacing: 0.22px;

		span {
			font-weight: 500;
		}
	}
}

.quiz {
	&__container {
		padding: 139px 0 211px;
	}

	&__title {
		font-size: 50px;
		line-height: 60px;
		font-weight: 300;
		text-align: center;
		margin-bottom: 74px;

		span {
			font-weight: 500;
		}
	}

	&__outer {
		max-width: 1293px;
		padding: 8px;
		margin: 0 auto;
		background-color: $white;
		border-radius: 10px;
	}

	&__inner {
		padding: 58px 86px 77px;
		border: 1px solid #f3f3f3;
		border-radius: 10px;
	}

	&__get-it {
		max-width: 346px;
		margin-right: 71px;
	}

	&__get-it-text {
		font-size: 25px;
		font-weight: 500;
		margin-bottom: 30px;
	}
}

.quiz-get {
	display: flex;
	align-items: center;
	width: 100%;
	height: 108px;
	padding: 20px 32px;
	background-color: #eff3f8;
	border-radius: 5px;
	position: relative;
	box-sizing: border-box;

	+ .quiz-get {
		margin-top: 24px;
	}

	&__value {
		margin-right: 18px;
		font-size: 47px;
		font-weight: 500;
		color: #89aace;
		text-shadow: 0 0 30px rgba(220, 222, 237, 0.18);
	}

	&__text {
		font-size: 18px;
		line-height: 24px;
		font-weight: 300;
		letter-spacing: 0.18px;

		span {
			font-weight: 500;
		}
	}

	&--with-pic {
		padding: 20px 81px;

		.quiz-get__picture {
			position: absolute;
			top: 2px;
			left: -40px;
		}

		&:last-child {
			.quiz-get__picture {
				top: -8px;
				left: -85px;
			}
		}
	}
}

.achievements {
	&__video {
		margin-top: -13px;
		padding-left: 21px;
		position: relative;
	}

	&__outer {
		max-width: 247px;
		padding: 6px;
		margin: 0 auto;
		background-color: $white;
		border-radius: 10px;
		box-sizing: border-box;
		position: absolute;
		top: 47.6%;
		left: 54.6%;
		transform: translate(-50%, -50%);
	}

	&__inner {
		padding: 23px 28px 23px 46px;
		border: 1px solid #f3f3f3;
		border-radius: 10px;

		p {
			font-size: 16px;
			line-height: 21.5px;
			color: #9f9f9f;
			font-weight: 300;

			span {
				font-weight: 500;
			}
		}
	}

	&__counter {
		position: relative;
		z-index: 1;
		display: flex;
		justify-content: center;
		margin-top: -91px;
	}

	&__counter-item {
		display: flex;
		align-items: center;
		width: 100%;
		max-width: 490px;

		&:not(:last-child) {
			margin-right: 20px;
		}
	}

	&__counter-img {
		display: flex;
		align-items: center;
		justify-content: center;
		max-width: 150px;
		width: 100%;
		height: 150px;
		margin-right: 35px;
		border-radius: 50%;
		overflow: hidden;

		img {
			object-fit: cover;
		}
	}

	&__digit {
		font-size: 50px;
		font-weight: 500;
		color: #87a8cd;
	}

	&__text {
		font-size: 23px;
		line-height: 29.88px;
		font-weight: 300;

		span {
			font-weight: 500;
		}
	}

	&__play {
		width: 135px;
		height: 135px;
		position: absolute;
		top: -9px;
		left: -103px;
		cursor: pointer;

		img {
			padding-left: 6px;
		}
	}

	.pulse-core {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;
		border-radius: 50%;
		background-color: #87a8cd;
		position: relative;
	}

	.pulse {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		border: 1px solid #fafafa;
		opacity: 0.3;
		border-radius: 50%;
		pointer-events: none;

		&-1 {
			animation: pulse-1 1s linear infinite;
		}

		&-2 {
			animation: pulse-2 1s linear infinite;
		}

		&-3 {
			animation: pulse-3 1s linear infinite;
		}

		&-4 {
			animation: pulse-4 1s linear infinite;
		}
	}
}

.products {
	&__container {
		padding: 145px 0 172px;
	}

	&__title {
		margin-bottom: 36px;
	}

	&__subtitle {
		font-size: 22px;
		font-weight: 300;
		color: #9f9f9f;
		text-align: center;
		letter-spacing: 0.8px;
		margin-bottom: 89px;
	}

	&__flex {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -37.5px;
	}

	&__outer {
		max-width: 563px;
		padding: 8px;
		margin: 17px auto 0;
		background-color: $white;
		border-radius: 10px;
		box-sizing: border-box;
	}

	&__inner {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 23px 36px 0 33px;
		border: 1px solid #f3f3f3;
		border-radius: 10px;

		img {
			width: 275px;
			margin-right: 26px;
		}

		p {
			font-size: 22px;
			line-height: 28px;
			font-weight: 500;
			letter-spacing: 0.3px;
			padding-bottom: 17px;
		}
	}
}

.product-item {
	width: calc(33.3333% - 75px);
	margin: 0 37.5px 70px;
	padding-bottom: 312px;
	background-color: $white;
	box-shadow: 0 0 30px 10px rgba(220, 222, 237, 0.33);
	border-radius: 10px;
	position: relative;
	overflow: hidden;
	box-sizing: border-box;

	&::before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 2;
		width: calc(100% - 16px);
		height: calc(100% - 16px);
		border: 1px solid #f3f3f3;
		border-radius: 10px;
		pointer-events: none;
	}

	&__img {
		border-radius: 10px;
		height: 405px;
		overflow: hidden;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
		}
	}

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		min-height: 312px;
		width: 100%;
		padding: 56px 60px 82px;
		position: absolute;
		bottom: 0;
		left: 0;
		z-index: 1;
		background-color: $white;
		box-sizing: border-box;

		&--expand {
			.product-item__title {
				font-weight: 500;
			}
		}
	}

	&__description {
		opacity: 0;
		transition: opacity 0.4s ease;

		p {
			font-size: 18px;
			line-height: 26.35px;
			font-weight: 300;
			text-align: center;

			&:nth-child(2) {
				margin-top: 20px;
				font-weight: 500;
			}

			&:last-child {
				margin-top: 20px;
				font-size: 25px;
				font-weight: 500;
				margin-bottom: 47px;
			}
		}

		&--visible {
			opacity: 1;
		}
	}

	&__button {
		width: 301px;
		height: 82px;
		margin: auto auto 0;
	}

	&__title {
		max-width: 300px;
		font-size: 25px;
		font-weight: 300;
		text-align: center;
		margin: 0 auto 28px;
	}

	&--social {
		padding: 89px 100px 57px;

		img {
			-webkit-filter: grayscale(100%);
			-moz-filter: grayscale(100%);
			-ms-filter: grayscale(100%);
			filter: grayscale(100%);
			opacity: 0.08;
			margin: 30px auto 0;
			width: 247px;
			padding-left: 20px;
		}

		.product-item {
			&__insta {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 56px;
				height: 56px;
				margin: 0 auto 10px;
				border-radius: 50%;
				overflow: hidden;
				background: #bc1888;
				background: -moz-linear-gradient(0deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
				background: -webkit-linear-gradient(0deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
				background: -ms-linear-gradient(0deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
				background: linear-gradient(0deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);

				svg {
					width: 50%;
					fill: $white;
				}
			}

			&__subs {
				text-align: center;

				p {
					&:first-of-type {
						font-size: 25px;
						line-height: 30.82px;
						font-weight: 500;
						margin-bottom: 17px;
					}

					&:nth-of-type(2) {
						font-size: 18px;
						line-height: 26.35px;
						font-weight: 300;
						margin-bottom: 35px;
					}

					&:last-of-type {
						font-size: 18px;
						font-weight: 300;
						opacity: 0.55;
						margin-top: 7px;
					}
				}

				a {
					font-size: 30px;
					font-weight: 500;
					color: $mainColor;
				}
			}
		}
	}
}