.header {
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 100;

	&__container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 158px;
		padding-right: 7px;
	}

	&__logo {
		width: 175px;
		height: 44px;

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__whats-done {
		font-size: 17px;
		line-height: 24.74px;
		font-weight: 500;
		letter-spacing: 0.09px;
		max-width: 232px;

		span {
			font-weight: 300;
		}
	}

	&__catalog {
		display: flex;
		align-items: center;
		margin-left: 15px;

		a {
			font-size: 18px;
			line-height: 24.74px;
			font-weight: 500;
			color: #59566a;
			letter-spacing: 0.09px;
			text-transform: uppercase;
			text-decoration: underline;
			margin-left: 10px;

			@media (min-width: $dMin) {
				&:hover {
					text-decoration: none;
				}
			}
		}
	}

	&__contacts,
	&__socials {
		display: flex;
		align-items: center;
	}

	&__contacts {
		font-size: 18px;
		font-weight: 300;
		letter-spacing: 0.14px;
		margin-left: 26px;
	}

	&__socials {
		margin: 0 37px 0 27px;
	}

	&__socials-icon {
		width: 45px;
		height: 41px;
		padding: 10px;
		border-radius: 5px;
		border: 1px solid $white;
		background-color: $white;
		box-sizing: border-box;
		transition: background-color 0.3s ease;

		@media (min-width: $dMin) {
			&:hover {
				background-color: rgba(255, 255, 255, 0.5);
			}
		}

		&:not(:last-child) {
			margin-right: 6px;
			padding: 7px;
		}

		svg {
			width: 100%;
			height: 100%;
		}
	}

	&__phone {
		font-size: 25px;
		font-weight: 500;
		letter-spacing: 0.63px;
		color: #0d323d;
	}
}